import instance from './instance'
const axios = instance('/c/cs')

export default class CollectionSuspensionServices {
  static async store(data, type = 'client') {
    return axios({
      url: `/collection/suspensions/${type}`,
      method: 'POST',
      data
    })
  }

  static async show(spnNumber, params = {}, type = 'client') {
    return axios({
      url: `/collection/suspensions/${type}/${spnNumber}`,
      method: 'GET',
      params
    })
  }

  static update(spnNumber, data, type = 'client') {
    return axios({
      url: `/collection/suspensions/${type}/${spnNumber}`,
      method: 'PUT',
      data
    })
  }

  static async getAvailableSoa(params = {}) {
    return axios({
      url: `/collection/suspension/available-soa`,
      method: 'GET',
      params
    })
  }
}