import React from 'react'
import { useHistory } from 'react-router-dom'
import Grid from '@material-ui/core/Grid'
import { Typography } from '@material-ui/core'
import noop from 'lodash/noop'

import Alert from 'SharedComponents/Alert/Alert'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import FormStepper from 'SharedComponents/FormStepper/FormStepper'
import PageFooter from './../../Components/PageFooter/PageFooter'
import CollectionHeader from './components/CollectionHeader'
import FirstStepComponent from './components/EndorseForSuspension'
import SecondStepComponent from './components/SuspensionBreakDown'

import CollectionSuspensionServices from 'Services/Api/CollectionSuspensionServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

import classes from './SuspensionPage.module.scss'

export default function SuspensionPage (props) {
  const history = useHistory()

  // TODO: Change labels and services based in data_type
  const { data_id = null, data_type = 'client' } = props.match.params
  const [stepper, setStepper] = React.useState({
    showActiveLabel: true,
    stepDetails: ['Analyze Details', 'Suspension Breakdown', 'Submit'],
    stepNumber: 1
  })
  const [pageTitle, setPageTitle] = React.useState('Endorse for Suspension')
  const [willSubmit, setWillSubmit] = React.useState(false)
  const [willCancel, setWillCancel] = React.useState(false)
  const [confirmed, setConfirmed] = React.useState(false)
  const [pageData, setPageData] = React.useState({
    availableSoa: [],
    spn_number: null,
    spn_soa: [],
    client: {}
  })

  const getData = () => {
    LoadingService.loading(true)
    Promise.all([CollectionSuspensionServices.show(data_id, {}, data_type)])
      .then(response => {
        LoadingService.loading(false)
        
        const [suspensionEndorsementResponse] = response
        const { client = {} } = suspensionEndorsementResponse.data.data
        
        setPageData(data => ({ 
          ...data, 
          ...suspensionEndorsementResponse.data.data 
        }))

        BreadcrumbsService.setSelectedItems([
          'collectionDistributionDashboard',
          'listOfAccountsWithAgingSummary',
          `clientAgingSummary:${client.account_name || ''}`,
          'suggestForSuspension'
        ])
      })
      .catch(error => {
        LoadingService.loading(false)
        console.error('Error fetching Suspension: ', error)
      })
  }

  const showComponent = componentProp => {
    // TODO: Add Member/Client Components
    switch (stepper.stepNumber) {
      case 1:
        return <FirstStepComponent {...componentProp} />
      case 2:
        return <SecondStepComponent {...componentProp} />
      case 3:
        return <h1>Step 3</h1>
      default:
        return ''
    }
  }

  const handleStepChange = stepNumber => {
    setStepper(prevState => ({ 
      ...prevState, 
      stepNumber, 
      onStepChanged: stepNumber 
    }))
  }

  const handlePageTitleChange = pageTitle => {
    setPageTitle(pageTitle)
  }

  const handleOnAddNewSOA = soaNumber => {
    if (!soaNumber) {
      return
    }

    LoadingService.loading(true)
    
    CollectionSuspensionServices.update(pageData.spn_number, {
      soas: [soaNumber]
    })
    .then(res => {
      LoadingService.loading(false)
      getData()
    })
    .catch(err => {
      console.error(`Error Updating Suspension: ${err}`)
      LoadingService.loading(false)
    })
  }

  const handleOnAddSOAClicked = () => {
    CollectionSuspensionServices.getAvailableSoa({
      spn_number: pageData.spn_number
    })
    .then(res => {
      const { results } = res.data.data
      setPageData(i => ({ ...i, availableSoa: results }))
    })
    .catch(err => console.error(`Error Fetching Available SOA(s): ${err}`))
  }

  const handleCancelSuspensionEndorsementConfirmed = () => {
    history.push('/')
  }

  const handleSubmitSuspensionEndorsementConfirmed = async () => {
    try {
      LoadingService.loading(true)

      // TODO: Update status of endorsement to submitted.

      setWillSubmit(false)
      setConfirmed(true)

      LoadingService.loading(false)
    } catch (error) {
      setWillSubmit(false)
      // TODO: Show alert message containing the error.
      LoadingService.loading(false)
    }
  }

  React.useEffect(() => {
    getData()
  }, [])

  return (
    <div className={classes.root}>
      <Grid container spacing={2} direction='column' justify='space-between'>
        <Grid item xs={12}>
          <Grid container className={classes.header} direction='row' justify='space-between'>
            <Grid item>
              <Typography id='suspension-page-header' variant='h5'>
                {pageTitle}
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <FormStepper {...stepper} />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              <CollectionHeader {...pageData} />
            </Grid>
            <Grid item xs={12}>
              {showComponent({
                handleOnAddNewSOA,
                handleOnAddSOAClicked,
                handleOnRemoveMember: noop,
                handlePageTitleChange,
                soa: pageData.spn_soa,
                availableSoa: pageData.availableSoa
              })}
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <PageFooter
            isStepper
            showSuspensionListBtn
            stepNumber={stepper.stepNumber}
            stepCount={stepper.stepDetails.length}
            handleStepChange={handleStepChange}

            submitSuspensionEndorsement={willSubmit}
            handleSubmitSuspensionEndorsementConfirmed={handleSubmitSuspensionEndorsementConfirmed}
            handleSubmitSuspensionEndorsementCancelled={() => setWillSubmit(false)}
            handleSubmitSuspensionEndorsement={() => setWillSubmit(true)}

            cancelSuspensionEndorsement={willCancel}
            handleCancelSuspensionEndorsement={() => setWillCancel(true)}
            handleCancelSuspensionEndorsementConfirmed={handleCancelSuspensionEndorsementConfirmed}
            handleCancelSuspensionEndorsementCancelled={() => setWillCancel(false)}

            confirmSuspensionEndorsement={confirmed}
            handleConfirmSuspensionEndorsementConfirmed={() => setConfirmed(false)}
          />
        </Grid>
      </Grid>
      <Alert />
    </div>
  )
}
